<template>
  <section class="section">
    <h2 class="section-title">üìñ Ê≠£ÊñáÂâßÊÉÖ üìñ</h2>
    <div class="content-text">
      <template v-for="seg in segments" :key="seg.key">
        <img v-if="seg.isImage" :src="seg.imageUrl" :alt="seg.altText" class="story-image" />
        <table v-else-if="seg.isTable" class="markdown-table">
          <thead>
            <tr>
              <th v-for="(header, idx) in seg.tableHeaders" :key="idx">{{ header }}</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(row, rowIdx) in seg.tableRows" :key="rowIdx">
              // eslint-disable-next-line vue/no-v-html
              <td v-for="(cell, cellIdx) in row" :key="cellIdx" v-html="formatTableCell(cell)"></td>
            </tr>
          </tbody>
        </table>
        <div v-else-if="seg.isSystem" class="system-message">
          <pre>{{ seg.text }}</pre>
        </div>
        <pre v-else-if="seg.className === 'image-prompt'" class="image-prompt">{{ seg.text }}</pre>
        <span v-else :class="seg.className">{{ seg.text }}</span>
      </template>
    </div>
  </section>
</template>

<script setup lang="ts">
type Segment = {
  key: string;
  text?: string;
  className?: string;
  isImage?: boolean;
  imageUrl?: string;
  altText?: string;
  isTable?: boolean;
  tableHeaders?: string[];
  tableRows?: string[][];
  isSystem?: boolean;
};

const props = defineProps<{
  content: string;
}>();

const segments = computed<Segment[]>(() => {
  const text = props.content ?? '';
  if (!text.trim()) {
    return [{ key: 'empty', text: '(ÊöÇÊó†Ê≠£Êñá)' }];
  }

  // Ë°®Ê†ºÊ≠£ÂàôÔºöÂåπÈÖçÂÆåÊï¥ÁöÑ markdown Ë°®Ê†º
  const tablePattern = /(?:^|\n)(\|[\s\S]*?\|)(?:\n\|[\s\S]*?\|)*/g;

  // Á≥ªÁªüÊ∂àÊÅØÊ≠£ÂàôÔºö>>> content <<< Ê†ºÂºèÔºàÂèØÂ∏¶ÂèØÈÄâÁöÑ ** Ê†áËÆ∞Ôºâ
  const systemPattern = /(\*{0,2}>>>(?:[\s\S]*?)<<<\*{0,2})/g;

  // ÂõæÁâáÊèêÁ§∫ËØçÊ≠£ÂàôÔºöimage###prompt### - ‰øùÁïôÊ†ºÂºè‰æõÁîüÂõæÊèí‰ª∂ÊèêÂèñ
  const imagePromptPattern = /image###([\s\S]*?)###/g;

  // ÁªòÂõæÊÄùËÄÉÊ†áÁ≠æÔºö<imgthink>...</imgthink> - ÈöêËóè‰∏çÊòæÁ§∫
  const imageThinkPattern = /<imgthink>[\s\S]*?<\/imgthink>/g;

  // ÈÄöÁî®Ê≠£ÂàôÔºöÂåπÈÖçÂõæÁâá„ÄÅÂØπËØù„ÄÅÊÄùËÄÉ„ÄÅÁ≥ªÁªüÊèêÁ§∫„ÄÅ‰ºäÁî∏/Á≥ªÁªüÂèëË®Ä
  const pattern = /!\[(.*?)\]\((.*?)\)|"([^"]+)"|\*([^*]+)\*|„Äê(.*?)„Äë|(?:^|\n)((?:‰ºäÁî∏|Á≥ªÁªü|System)[Ôºö:].*?)(?=$|\n)/g;
  const result: Segment[] = [];

  let lastIndex = 0;

  // ÂÖàÂ§ÑÁêÜË°®Ê†º
  for (const tableMatch of text.matchAll(tablePattern)) {
    const index = tableMatch.index ?? 0;

    // Â§ÑÁêÜË°®Ê†º‰πãÂâçÁöÑÊôÆÈÄöÊñáÊú¨
    if (index > lastIndex) {
      result.push({ key: `t${result.length}`, text: text.slice(lastIndex, index) });
    }

    // Ëß£ÊûêË°®Ê†º
    const tableContent = tableMatch[0];
    const rows = tableContent
      .trim()
      .split('\n')
      .filter(row => row.trim().startsWith('|'));

    if (rows.length >= 2) {
      // ÂéªÊéâÂàÜÈöîË°åÔºà|---|---Ôºâ
      const dataRows = rows.filter(row => !row.includes('---') && !row.includes('---'));

      if (dataRows.length >= 1) {
        const headers = parseTableRow(dataRows[0]);
        const tableRows = dataRows.slice(1).map(parseTableRow);

        result.push({
          key: `table${result.length}`,
          isTable: true,
          tableHeaders: headers,
          tableRows: tableRows,
        });
      }
    }

    lastIndex = index + tableMatch[0].length;
  }

  // Â§ÑÁêÜÁªòÂõæÊÄùËÄÉÊ†áÁ≠æ <imgthink> - ÈöêËóè‰∏çÊòæÁ§∫
  for (const thinkMatch of text.matchAll(imageThinkPattern)) {
    const index = thinkMatch.index ?? 0;

    // Â§ÑÁêÜ‰πãÂâçÁöÑÂÜÖÂÆπ
    if (index > lastIndex) {
      result.push({ key: `t${result.length}`, text: text.slice(lastIndex, index) });
    }

    // Ë∑≥ËøáÔºå‰∏çÊ∑ªÂä†Âà∞ÁªìÊûú‰∏≠ÔºàÈöêËóèÔºâ
    lastIndex = index + thinkMatch[0].length;
  }

  // Â§ÑÁêÜÂõæÁâáÊèêÁ§∫ËØç image###prompt### - ‰øùÁïôÊ†ºÂºè‰Ωú‰∏∫‰ª£Á†ÅÂùóÊòæÁ§∫
  for (const imgMatch of text.matchAll(imagePromptPattern)) {
    const index = imgMatch.index ?? 0;

    // Â§ÑÁêÜ‰πãÂâçÁöÑÂÜÖÂÆπ
    if (index > lastIndex) {
      result.push({ key: `t${result.length}`, text: text.slice(lastIndex, index) });
    }

    // ‰Ωú‰∏∫‰ª£Á†ÅÂùó‰øùÁïôÔºå‰æõÁîüÂõæÊèí‰ª∂ÊèêÂèñ
    result.push({
      key: `imgprompt${result.length}`,
      text: imgMatch[0],
      className: 'image-prompt',
    });

    lastIndex = index + imgMatch[0].length;
  }

  // Â§ÑÁêÜÁ≥ªÁªüÊ∂àÊÅØÔºà>>> content <<<Ôºâ
  for (const systemMatch of text.matchAll(systemPattern)) {
    const index = systemMatch.index ?? 0;

    // Â§ÑÁêÜÂÖ¨Âëä‰πãÂâçÁöÑÊôÆÈÄöÊñáÊú¨
    if (index > lastIndex) {
      result.push({ key: `t${result.length}`, text: text.slice(lastIndex, index) });
    }

    // Ê∏ÖÁêÜÂÜÖÂÆπÔºöÁßªÈô§Âë®Âõ¥ÁöÑ >>> Âíå <<<Ôºå‰ª•ÂèäÂèØÈÄâÁöÑ **
    let content = systemMatch[1];
    content = content.replace(/^\*{0,2}>>>\s*/, '').replace(/\s*<<<\*{0,2}$/, '');

    result.push({
      key: `system${result.length}`,
      text: content,
      isSystem: true,
      className: 'system-message',
    });

    lastIndex = index + systemMatch[0].length;
  }

  // Â§ÑÁêÜÂâ©‰ΩôÁöÑÈùûË°®Ê†º„ÄÅÈùûÁ≥ªÁªüÊ∂àÊÅØÂÜÖÂÆπ
  if (lastIndex < text.length) {
    const remainingText = text.slice(lastIndex);
    for (const match of remainingText.matchAll(pattern)) {
      const index = (match.index ?? 0) + lastIndex;

      // Â§ÑÁêÜÂåπÈÖçÈ°π‰πãÂâçÁöÑÊôÆÈÄöÊñáÊú¨
      if (index > lastIndex) {
        result.push({ key: `t${result.length}`, text: remainingText.slice(lastIndex - lastIndex, match.index) });
      }

      if (match[2] != null) {
        // ÂõæÁâá ![]() - match[1]ÂèØËÉΩÊòØalt textÔºàÂèØÁ©∫ÔºâÔºåmatch[2]ÊòØURL
        result.push({
          key: `img${result.length}`,
          text: match[0],
          isImage: true,
          altText: match[1] ?? '',
          imageUrl: match[2],
        });
      } else if (match[6] != null) {
        // ‰ºäÁî∏ÔºöÊ∂àÊÅØ Êàñ Á≥ªÁªüÔºöÊ∂àÊÅØÔºàÁªü‰∏Ä‰∏∫Á≥ªÁªüÊ∂àÊÅØÊ†∑ÂºèÔºâ
        result.push({
          key: `system${result.length}`,
          text: match[6],
          isSystem: true,
          className: 'system-message',
        });
      } else if (match[5] === 'Á≥ªÁªü') {
        // „ÄêÁ≥ªÁªü„ÄëÊ∂àÊÅØ - ÈªÑËâ≤ÂçïÁã¨Ê†∑Âºè
        result.push({
          key: `t${result.length}`,
          text: '„ÄêÁ≥ªÁªü„ÄëÊ∂àÊÅØ',
          className: 'system-hint',
        });
      } else if (match[5] != null) {
        // „ÄêËßíËâ≤Âêç„Äë- ËìùËâ≤È´ò‰∫ÆÊ†∑Âºè
        result.push({
          key: `t${result.length}`,
          text: `„Äê${match[5]}„Äë`,
          className: 'character-name',
        });
      } else if (match[3] != null) {
        // "‰∏≠ÊñáÂØπËØù"
        result.push({
          key: `t${result.length}`,
          text: `‚Äú${match[3]}"`,
          className: 'dialog-text',
        });
      } else if (match[4] != null) {
        // "Ëã±ÊñáÂØπËØù"
        result.push({
          key: `t${result.length}`,
          text: `"${match[4]}"`,
          className: 'dialog-text',
        });
      }

      lastIndex = index + match[0].length;
    }

    if (lastIndex < text.length) {
      result.push({ key: `t${result.length}`, text: text.slice(lastIndex) });
    }
  }

  return result;
});

// Ëß£ÊûêË°®Ê†ºË°å
function parseTableRow(row: string): string[] {
  return row
    .trim()
    .split('|')
    .slice(1, -1) // ÂéªÊéâÈ¶ñÂ∞æÁöÑÁ©∫Â≠óÁ¨¶‰∏≤
    .map(cell => cell.trim());
}

// Ê†ºÂºèÂåñË°®Ê†ºÂçïÂÖÉÊ†ºÔºàÊîØÊåÅÁ≤ó‰Ωì„ÄÅÊñú‰ΩìÁ≠âÔºâ- ÂÆâÂÖ®Â§ÑÁêÜÈò≤Ê≠¢ XSS
function formatTableCell(cell: string): string {
  // ÂÖàËΩ¨‰πâ HTML ÁâπÊÆäÂ≠óÁ¨¶
  const safe = cell
    .replace(/&/g, '&amp;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#039;');

  // ÂÜçÂ§ÑÁêÜÊîØÊåÅÁöÑÊ†ºÂºè
  return safe
    .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
    .replace(/\*(.*?)\*/g, '<em>$1</em>')
    .replace(/„Äê(.*?)„Äë/g, '„Äê$1„Äë');
}
</script>

<style scoped>
.markdown-table {
  width: 100%;
  border-collapse: collapse;
  margin: 12px 0;
  font-size: 0.9em;
  background-color: var(--bg-medium);
  border-radius: 8px;
  overflow: hidden;
}

.markdown-table th,
.markdown-table td {
  padding: 10px 12px;
  text-align: left;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.markdown-table th {
  background-color: var(--bg-dark);
  color: var(--accent-gold);
  font-weight: bold;
  font-size: 0.85em;
}

.markdown-table td {
  color: var(--text-color);
  line-height: 1.5;
}

.markdown-table td :deep(strong) {
  color: var(--text-strong);
}

.markdown-table td :deep(em) {
  color: var(--accent-blue);
}

.markdown-table tr:last-child td {
  border-bottom: none;
}

.markdown-table tbody tr:hover {
  background-color: rgba(255, 255, 255, 0.03);
}

/* Á≥ªÁªüÊ∂àÊÅØÊ†∑ÂºèÔºà‰ºäÁî∏ÔºöÊ∂àÊÅØ„ÄÅ>>> Ê∂àÊÅØ <<<Ôºâ */
.system-message {
  background-color: rgba(0, 180, 216, 0.1);
  border: 1px solid rgba(0, 180, 216, 0.4);
  border-radius: 8px;
  padding: 12px 16px;
  margin: 12px 0;
  font-family: 'Consolas', 'Monaco', 'Courier New', monospace;
  font-size: 0.9em;
  line-height: 1.6;
  color: var(--accent-cyan, #00b4d8);
  overflow-x: auto;
}

.system-message pre {
  margin: 0;
  white-space: pre-wrap;
  word-wrap: break-word;
}

/* „ÄêÁ≥ªÁªü„ÄëÊ∂àÊÅØÊ†∑Âºè - ÈªÑËâ≤ */
.system-hint {
  color: var(--accent-gold, #f1fa8c);
  font-weight: 500;
}

/* „ÄêËßíËâ≤Âêç„ÄëÊ†∑Âºè - ËìùËâ≤È´ò‰∫Æ */
.character-name {
  color: var(--accent-blue, #bd93f9);
  font-weight: 600;
}

/* ÂõæÁâáÊèêÁ§∫ËØçÊ†∑Âºè - ‰ª£Á†ÅÂùóÂΩ¢Âºè‰øùÁïô‰æõÊèí‰ª∂ÊèêÂèñ */
.image-prompt {
  display: block;
  background-color: rgba(255, 255, 255, 0.05);
  border: 1px dashed rgba(255, 255, 255, 0.2);
  border-radius: 4px;
  padding: 8px 12px;
  margin: 8px 0;
  font-family: 'Consolas', 'Monaco', 'Courier New', monospace;
  font-size: 0.85em;
  color: var(--accent-gold);
  overflow-x: auto;
}
</style>
