<script setup lang="ts">
import { ref, computed } from 'vue';
import { useDataStore } from '../../store';

const store = useDataStore();
const isGoalsExpanded = ref(false);
const isIntelExpanded = ref(false);

// Èò∂ÊÆµÁõÆÊ†áÂÆåÊàêÁä∂ÊÄÅÂ≠òÂÇ®
const completedGoals = computed(() => store.data.‰∏ªÁ∫ø‰ªªÂä°.Èò∂ÊÆµÁõÆÊ†á.filter((_, idx) => isGoalCompleted(idx)).length);

const progressPercent = computed(() => {
  const total = store.data.‰∏ªÁ∫ø‰ªªÂä°.Èò∂ÊÆµÁõÆÊ†á.length;
  if (total === 0) return 0;
  return Math.round((completedGoals.value / total) * 100);
});

// ‰ªé store ËØªÂèñÁõÆÊ†áÂÆåÊàêÁä∂ÊÄÅ
function isGoalCompleted(idx: number): boolean {
  const key = String(idx);
  return store.data.‰∏ªÁ∫ø‰ªªÂä°.ÁõÆÊ†áÂÆåÊàêÁä∂ÊÄÅ[key] === true;
}

// ÊÉÖÊä•Á¢éÁâáÁªüËÆ°
const intelTotal = computed(() => Object.keys(store.data.‰∏ªÁ∫ø‰ªªÂä°.ÊÉÖÊä•Á¢éÁâá).length);
const intelCompleted = computed(
  () => Object.values(store.data.‰∏ªÁ∫ø‰ªªÂä°.ÊÉÖÊä•Á¢éÁâá).filter(i => i.Áä∂ÊÄÅ === 'Â∑≤ÂÆåÊàê').length,
);

function getStatusIcon(status: string): string {
  switch (status) {
    case 'Â∑≤ÂÆåÊàê':
      return '‚úì';
    case 'Â∑≤Êé¢Á¥¢':
      return '‚óê';
    case 'Êú™Êé¢Á¥¢':
      return '‚óã';
    default:
      return '‚óã';
  }
}

function getRingProgress(status: string): string {
  let progress = 33;
  switch (status) {
    case 'Â∑≤ÂÆåÊàê':
      progress = 100;
      break;
    case 'Â∑≤Êé¢Á¥¢':
      progress = 66;
      break;
    case 'Êú™Êé¢Á¥¢':
      progress = 33;
      break;
  }
  const circumference = 2 * Math.PI * 16;
  const offset = circumference - (progress / 100) * circumference;
  return `${progress} ${circumference} ${offset}`;
}
</script>

